@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@400;600;700;900&display=swap);
:root {
    --head-padding: 30px;
}

.header {
    display: flex;
    position: fixed;
    top: 0;
    width: 100%;
    height: 100px;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 -30px 40px 1px black;
    background: white;
}

.header a {
    height: 80%;
    cursor: pointer;
    margin-left: 30px;
    margin-left: var(--head-padding);
}

.header img {
    height: 80%;
    cursor: pointer;
    margin-left: 30px;
    margin-left: var(--head-padding);
}

.navbar-items {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 70%;
    padding-right: 30px;
    padding-right: var(--head-padding);
}

.navbar-items a {
    position: relative;
    text-decoration: none;
    color: black;
    font-size: 20px;
    font-weight: 600;
}

.navbar-items a::after {
    content: '';
    background: black;
    position: absolute;
    width: 100%;
    display: block;
    margin: auto;
    height: 1px;
    bottom: 0;
    will-change: transform;
    transition: transform .3s ease;
    transform: scaleX(0);
}

.navbar-items a.selected::after {
    transform: scaleX(1.2);
}

.navbar-items a:hover::after {
    transform: scaleX(1);
}

.btn {
    outline: none;
    border: none;
    padding: 5px 15px;
    background: black;
    border-radius: 5px;
    color: white;
    font-weight: bold;
    font-family: 'Cairo', sans-serif;
    font-size: 20px;
    cursor: pointer;
    transition: .3s ease;
}

.btn:hover {
    background: #525252;
}

.burger {
    cursor: pointer;
    display: none;
    width: 40px;
    height: 40px;
    margin-right: 40px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.burger span {
    width: 90%;
    height: 3px;
    background: black;
    margin: 2px;
    transition: .3s ease;
}

.opened-burger .first-span {
    transform: rotate(45deg) translate(5px, 5px)
}

.opened-burger .second-span {
    transform: rotate(-45deg);
}

.search-bar input[type="text"] {
    padding: 15px 40px 15px 20px;
    width: 200px;
    margin: 10px 0;
    color: #525252;
    font-size: 16px;
    font-weight: 100;
    letter-spacing: 2px;
    border: solid 1px gray;
    border-radius: 5px;
    outline: none;
}

.search-bar i {
    position: relative;
    left: -37px;
    cursor: pointer;
    color: #005fc5;
}

.form {
    width: 300px;
    height: 400px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 40px 30px 20px 30px;
}

.form input {
    width: 70%;
    padding: 5px;
    border: solid grey 1px;
    border-radius: 4px;
}

.form textarea {
    width: 70%;
    resize: none;
    height: 200px;
    padding: 5px;
    border: solid grey 1px;
    border-radius: 4px;
}

.form span {
    height: 25px !important;
    color: red;
}

.form .btn{
    margin: 20px 20px;
}

@media only screen and (max-width: 1024px) {
    .navbar-items {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        width: 100%;
        position: absolute;
        padding-top: 50px;
        top: 100%;
        opacity: 0;
        visibility: hidden;
        background: white;
        transition: 0.5s ease;
        border-top: solid 1px gray;
    }

    .navbar-items.opened {
        opacity: 1;
        visibility: visible;
    }

    .navbar-items a {
        margin-bottom: 20px;
    }

    .header img {
        height: 50%;
    }

    .burger {
        display: flex;
    }

    .navbar-items .btn{
        margin-bottom: 100px;
    }
}

.article-container{
    width: 80%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 10px;
    overflow: hidden;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    background: white;
    margin: auto;
    display: flex;
}

.article-image-container{
    width: 80%;
}

.article-image-container img{
    object-fit: cover;
    overflow: hidden;
    height: 100%;
    width: 100%;
}

.article-texts{
    padding: 20px;
    width: 100%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}

.article-texts p{
    margin: 50px 0;
}

.article-texts div:nth-child(4) a:nth-child(2){
    float: right;
    text-decoration: underline;
    cursor: pointer;
}

.article-texts button{
    float: right;
    margin-top: 10px;
}

@media only screen and (max-width: 1024px) {

    .article-texts{
        padding: 5px;
    }

    .article-texts p{
        margin: 10px 0;
        font-size: 13px;
    }

    .article-texts h1{
        font-size: 17px;
    }

    .article-texts h2{
        font-size: 15px;
    }

    .article-texts span{
        font-size: 13px;
    }

}


.article-item-container {
    width: 60%;
    height: 370px;
    border: solid gray;
    margin: 50px auto;
    display: flex;
    border-radius: 20px;
    overflow: hidden;
}

.article-item-image-container {
    width: 40%;
    height: 100%;
}

.article-item-image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.article-item-texts-container {
    width: 60%;
    padding: 30px;
}

.article-item-texts-container p{
    margin-top: 70px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
}

.article-item-texts-container span{
    float: right;
    text-decoration: underline;
    font-size: 20px;
    cursor: pointer;
}

@media only screen and (max-width: 1024px) {
    .article-item-container {
        width: 90%;
        height: 300px;
    }

    .article-item-texts-container h2{
        font-size: 16px;
    }

    .article-item-texts-container p{
        margin-top: 20px;
    }

}
.news-list-container{
    margin-top: 150px;
}
.error-banner {
    padding: 60px 0 20px;
    display: inline-block;
    font-family: Helvetica-neue, Helvetica, Arial, sans-serif;
    color: #788392;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
}
.error-banner svg {
    position: relative;
    overflow: visible;
    z-index: 2;
    transform: translate3d(0, 0, 0);
    margin: 3em;
}
.error-banner .error-message {
    margin: 1em 0;
    position: relative;
}
.error-banner h2 {
    font-size: 50px;
    font-weight: normal;
    margin: 0;
    -webkit-animation: 1.5s ease-out fade-in-slowly;
    animation: 1.5s ease-out fade-in-slowly;
}
.error-banner p {
    font-size: 20px;
    margin: 8px;
}
.error-banner p span {
    -webkit-animation: 2s ease-out fade-in-slowly;
    animation: 2s ease-out fade-in-slowly;
}
.error-banner svg:hover + .error-message {
    -webkit-animation: error-party .9s linear infinite;
    animation: error-party .9s linear infinite;
}
.error-banner svg:hover + .error-message h2 {
    font-size: 0;
}
.error-banner svg:hover + .error-message h2:before {
    font-size: 20px;
    content: "On the bright side, you found";
}
.error-banner svg:hover + .error-message p {
    font-size: 0;
}
.error-banner svg:hover + .error-message p:after {
    font-size: 50px;
    line-height: 1.2em;
    content: "THE PARTY!";
}
.error-banner svg:hover + .error-message p {
    -webkit-animation: zbounce .5s linear infinite;
    animation: zbounce .5s linear infinite;
}

